<template>
  <div>
    <div class='left'>
      <ul>
        <li id='logo'>
          <div>Shop</div>
          <div>Anam</div>
        </li>
        <li v-for="list in linklist" v-bind:key="list.content">
          <router-link v-bind:to='list.link'>{{ list.content}}</router-link>
        </li>
        <li id='copyright'>
          <div>Yunseo Park</div>
          <div>Seungmin Yoon</div>
        </li>
      </ul>
    </div>
    <img id='left_btn' @click='toggle' src='../assets/left_btn.png'>
  </div>
</template>
<script>
export default {
  name: 'left',
  data () {
    return {
      toggle: this.enter,
      linklist: [
        {content: 'Home', link: '/'},
        {content: 'Login', link: '/login'},
        {content: 'SignUp', link: '/signUp'},
        {content: 'List', link: '/list'}
      ]
    }
  },
  methods: {
    enter: function (el, done) {
      this.toggle = this.leave
      var left = (250 - (parseInt($('#left_btn').css('width')) / 2)) + 'px'
      $('.left').stop().animate({left: '0px'}, 800)
      $('#left_btn').stop().animate({left: left}, 800)
      $('#left_btn').css({transform: 'rotate(180deg)'})
    },
    leave: function (el, done) {
      this.toggle = this.enter
      var left = (0 - (parseInt($('#left_btn').css('width')) / 2)) + 'px'
      $('.left').stop().animate({left: '-250px'}, 800)
      $('#left_btn').stop().animate({left: left}, 800)
      $('#left_btn').css({transform: 'rotate(0deg)'})
    },
    resize: function () {
      $(window).resize(function () {
        var height = (window.innerHeight / 2) + 'px'
        $('#left_btn').css({'top': height})
      })
    }
  },
  created () {
    this.resize()
  }
}
</script>

<style>
  a{color:black}
  a:hover{color:rgb(255,98,124);}
.left{
   position:fixed;
   top:0px;
   left:-250px;
   width:250px;
   height:1000px;
   background:rgba(255,98,124,0.6);
   padding:40px 0;
 }
 .left ul {padding: 0 30px}
 .left ul li { font-size:25px;  height:75px;}
 .left ul li#logo {font-family: 'Passion One', cursive; font-size:50px; height:170px;}
 .left ul li#logo div {line-height: 0.8}
 .left ul li#company {font-size:16px; margin-top:150px; }
 #left_btn {position:fixed; top:495px; left:-60px; cursor:pointer}

</style>



