<template lang="html">
  <div id="SignUp">
    <h3>회원가입</h3>
    <div> Sign up </div>
    name : <input v-model="user.name" placeholder="name"> <br />
    ID : <input v-model="user.uid" placeholder="ID"> <br />
    Password : <input v-model="user.password" type="password"  placeholder="password">
    <button v-on:click="signUp" >SignUp</button>
    <router-link to="/"><p>HOME</p></router-link>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      user: {
        uid: '',
        password: '',
        name: ''
      }
    }
  },
  methods: {
    signUp: function (event) {
      this.$http.post('/api/signUp', {user: this.user})
        .then((response) => {
          alert(this.user.uid)
          if (response.data.result === 0) {
            alert('Error, please, try again')
          }
          if (response.data.result === 1) {
            alert('Success')
            this.$router.push('/login') // Login 페이지로 보내줌
          }
        })
        .catch(function (error) {
          if (error) {
            alert('error1')
          }
        })
    }
  }
}
</script>

<style lang="css">
</style>



